import { Button, Modal } from "react-bootstrap"


function ModalVideoKycSet(props) {
    return (
        <>
            <Modal
                {...props}
                size="lg"
                aria-labelledby="contained-modal-title-vcenter"
                backdrop="static"
            >
                <Modal.Header >
                    <Modal.Title id="contained-modal-title-vcenter" style={{ color: '2e3191' }}>
                        Video KYC Instructions /   
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <p style={{ lineHeight: '37px', fontWeight: '700' }}>
                    My name is . My shop name is  I want to join NEEDU and I am sharing my KYC documents to complete the process.            NEEDU    /    KYC         / 
                    </p>
                </Modal.Body>
                <Modal.Footer>
                    <Button onClick={props.onHide} >Agree</Button>
                </Modal.Footer>
            </Modal>
        </>
    )
}
export default ModalVideoKycSet